<template>
  <div class="dis-connected">
    
    <van-button type="warning" @click="$router.go(0)"></van-button>
  </div>
</template>

<script>
  export default {
    name: 'DisConnected'
  }
</script>

<style lang="less" scoped>
  .dis-connected {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: pink;
    z-index: 9999;
  }
</style>



