import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home/home'
import Goods from '@/views/goods/goods'
import Seller from '@/views/seller/seller'
import Ratings from '@/views/ratings/ratings'
import Demo from '@/views/demo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: '',
      component: Home,
      redirect: '/home/goods',
      children: [
        {
          path: '/home/goods',
          name: '',
          component: Goods
        },
        {
          path: '/home/seller',
          name: '',
          component: Seller
        },
        {
          path: '/home/ratings',
          name: '',
          component: Ratings
        }
      ]
    },
    {
      path: '/demo',
      name: '',
      component: Demo
    }
  ],
})



