<template>
  <div class="cart-control">
    <!--v-if,v-showtemplatev-else-->
    <transition name="move">
      <div class="left-wrapper" v-if="visible">
        <div class="left-button" @click="reduce">
          <base-icon icon="reduce"></base-icon>
        </div>
        <div class="number">{{food.count}}</div>
      </div>
    </transition>
    <div class="right-button" @click="plus">
      <base-icon icon="plus"></base-icon>
      <transition v-if="position"
                  @before-enter="beforeEnter"
                  @enter="enter"
                  @after-enter="afterEnter">
        <div class="ball-container" v-show="visibleBall">
          <div class="ball" ref="ball"></div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
	name: 'CartControl',
	props: {
		food: {
			type: Object,
			default() {
				return { count: 0 }
			}
		},
		position: {
			type: Object,
			required: false
		}
	},
	data() {
		return {
			copyFood: {},
			visibleBall: false,
			oBall: {}
		}
	},
	computed: {
		visible() {
			return this.food.count > 0
		}
	},
	mounted() {
		this.oBall = this.$refs.ball
	},
	methods: {
		reduce() {
			this.copyFood = JSON.parse(JSON.stringify(this.food))
			this.copyFood.count--
			console.log('food', this.copyFood)
			this.$emit('updateFood', this.copyFood)
		},
		plus() {
			this.visibleBall = true
			this.copyFood = JSON.parse(JSON.stringify(this.food))
			if (!this.copyFood.count) {
				this.copyFood.count = 0
			}
			this.copyFood.count++
			this.$emit('updateFood', this.copyFood)
		},
		beforeEnter(el) {
			el.style.transform = `translate(0)`
		},
		enter(el) {
			const rect = el.getBoundingClientRect()
			const x = rect.x - this.position.x + rect.width / 2
			const y = this.position.y - rect.y - rect.height / 2
			el.style.transform = `translate(-${x}px)`
			this.oBall.style.transform = `translate(0,${y}px)`
		},
		afterEnter(el) {
			el.style.transform = `translate(0)`
			this.oBall.style.transform = `translate(0,0px)`
			el.style.display = 'none'
			this.visibleBall = false
		}
	}
}
</script>

<style lang="less" scoped>
@import '~styles/varibale';

.cart-control {
	display: flex;
	align-items: center;
	.left-wrapper {
		display: flex;
		align-items: center;
	}
	.right-button,
	.left-button {
		font-size: 24px;
		border-radius: 50%;
		color: @blue;
		transition: all 0.4s;
	}
	.right-button {
		position: relative;
	}
	.ball-container {
		position: absolute;
		z-index: 4;
		top: 0;
		right: 0;
		height: 100%;
		width: 100%;
		transition: all 0.3s linear;
	}
	.ball {
		height: 100%;
		width: 100%;
		border-radius: 50%;
		background-color: #00a0dc;
		transition: all 0.3s cubic-bezier(0.2, -0.17, 0.41, -0.16);
	}
	.number {
		width: 0.48rem;
		text-align: center;
		font-size: 12px;
		color: #93999f;
	}
	/**/
	.move-enter,
	.move-leave-to {
		transform: translateX(100%);
		opacity: 0;
		.left-button {
			transform: rotate(360deg);
		}
	}
	/*2active
      
    */
	.move-enter-active,
	.move-leave-active {
		transition: all 0.4s;
	}
}
</style>



